import type { LeftToolbarProps } from './types';

export const PREVENT_MOUSE_MOVE = Symbol('PREVENT_MOUSE_MOVE');
export const CLEAR_PREVENT = Symbol('CLEAR_PREVENT');
export const UPDATE_HOVER_BLOCK = Symbol('UPDATE_HOVER_BLOCK');

export function preventMouseMove() {
  return {
    type: PREVENT_MOUSE_MOVE,
  };
}

export function clearPrevent() {
  return {
    type: CLEAR_PREVENT,
  };
}

export function updateHoverBlock(payload?: LeftToolbarProps) {
  return {
    type: UPDATE_HOVER_BLOCK,
    payload,
  };
}
